import React, { useState, useEffect } from 'react';
import { Typography, TextField, Button, Grid, MenuItem, Select, FormControl, InputLabel, Autocomplete } from '@mui/material';

import MainCard from 'ui-component/cards/MainCard';

const Page = () => {
  const [token, setToken] = useState('');

  const [outputError, setOutputError] = useState('');
  const [outputData, setOutputData] = useState('');

  const [menuItems, setMenuItems] = useState([]);
  const [selectedChannel, setSelectedChannel] = useState('');
  const [selectedType, setSelectedType] = useState('');
  const [additionalElement, setAdditionalElement] = useState(null);
  const [searchResults, setSearchResults] = useState([]);
  const [selectedUser, setSelectedUser] = useState('');

  useEffect(() => {
    document.title = `LsCo Software - Channel Manager`;
  }, []);

  const addToken = async () => {
    if (token.trim() === '') {
      setOutputError('You must specify an OAuth Token');
      setOutputData('');
      return;
    } else {
      try {
        const tokens = JSON.parse(localStorage.getItem('tokens')) || [];

        const validate = await validateToken(token.replace(/^oauth[:]*/i, ''));

        if (tokens.find((xyz) => xyz.id === validate.user_id.toLowerCase())) {
          setOutputError('The channel already exists');
          setOutputData('');
          return;
        }

        tokens.push({
          id: validate.user_id,
          login: validate.login,
          access_token: token.replace(/^oauth[:]*/i, ''),
          client_id: validate.client_id
        });

        localStorage.setItem('tokens', JSON.stringify(tokens));

        updateChannels();

        setOutputData('');
        setOutputData(`Successfully added login: ${validate.login} - ID: ${validate.user_id}`);
        return;
      } catch (err) {
        setOutputError(err.message);
        setOutputData('');
        return;
      }
    }
  };

  const updateChannels = () => {
    const tokens = JSON.parse(localStorage.getItem('tokens')) || [];
    const options = tokens.map(({ login, id }) => ({
      value: id,
      label: `${login} - ID: ${id}`
    }));

    if (options.length >= 1) {
      setMenuItems(options);
    } else {
      setMenuItems([{ value: '', label: 'No Channels' }]);
    }
  };

  const handleSearchChange = async (value) => {
    if (value.trim() !== '') {
      const response = await fetch(`https://api.twitch.tv/helix/search/channels?query=${value}`, {
        headers: {
          'Client-Id': '9uavest5z7knsvpbip19fxqkywxz3ec'
        }
      });

      const data = await response.json();

      if (response.ok) {
        setSearchResults(data.data.map((channel) => `${channel.display_name} - ID: ${channel.id}`));
      } else {
        setSearchResults([]);
      }
    } else {
      setSearchResults([]);
    }
  };

  useEffect(() => {
    updateChannels();
  }, []);

  useEffect(() => {
    setOutputError('');
    setOutputData('');

    const getMods = async () => {
      if (!selectedChannel) {
        setOutputError('No channel specified');
        setOutputData('');
        return [];
      }

      const tokens = JSON.parse(localStorage.getItem('tokens')) || [];

      const index = tokens.findIndex((x) => x.id === selectedChannel);

      let url = `https://api.twitch.tv/helix/moderation/moderators?first=100&broadcaster_id=${tokens[index].id}`;
      let cursor = null;
      let moderators = [];

      for (;;) {
        if (cursor) {
          url = `https://api.twitch.tv/helix/moderation/moderators?first=100&broadcaster_id=${tokens[index].id}&after=${cursor}`;
        }

        const response = await fetch(url, {
          headers: {
            Authorization: `Bearer ${tokens[index].access_token}`,
            'Client-Id': tokens[index].client_id
          }
        });

        const data = await response.json();

        if (!response.ok) {
          return [];
        }

        moderators.push(...data.data.map((i) => `${i.user_name} - ID: ${i.user_id}`));

        if (data.pagination && data.pagination.cursor) {
          cursor = data.pagination.cursor;
        } else {
          break;
        }
      }

      return moderators;
    };

    const getVips = async () => {
      if (!selectedChannel) {
        setOutputError('No channel specified');
        setOutputData('');
        return [];
      }

      const tokens = JSON.parse(localStorage.getItem('tokens')) || [];

      const index = tokens.findIndex((x) => x.id === selectedChannel);

      let url = `https://api.twitch.tv/helix/channels/vips?first=100&broadcaster_id=${tokens[index].id}`;
      let cursor = null;
      let vips = [];

      for (;;) {
        if (cursor) {
          url = `https://api.twitch.tv/helix/channels/vips?first=100&broadcaster_id=${tokens[index].id}&after=${cursor}`;
        }

        const response = await fetch(url, {
          headers: {
            Authorization: `Bearer ${tokens[index].access_token}`,
            'Client-Id': tokens[index].client_id
          }
        });

        const data = await response.json();

        if (!response.ok) {
          return [];
        }

        vips.push(...data.data.map((i) => `${i.user_name} - ID: ${i.user_id}`));

        if (data.pagination && data.pagination.cursor) {
          cursor = data.pagination.cursor;
        } else {
          break;
        }
      }

      return vips;
    };

    const removeVip = async () => {
      if (!selectedChannel) {
        setOutputError('No channel specified');
        setOutputData('');
        return;
      }

      const tokens = JSON.parse(localStorage.getItem('tokens')) || [];

      const index = tokens.findIndex((x) => x.id === selectedChannel);

      const request = await fetch(
        `https://api.twitch.tv/helix/channels/vips?broadcaster_id=${tokens[index].id}&user_id=${selectedUser.split('- ID: ')[1]}`,
        {
          method: 'DELETE',
          headers: {
            Authorization: `Bearer ${tokens[index].access_token}`,
            'Client-Id': tokens[index].client_id
          }
        }
      );

      if (!request.ok) {
        const data = await request.json();

        setOutputError(data.message);
        setOutputData('');
        return;
      }

      setOutputData('');
      setOutputData(`Successfully removed ${selectedUser.split('- ID: ')[0]} as VIP in ${tokens[index].login}`);
      return;
    };

    const removeMod = async () => {
      if (!selectedChannel) {
        setOutputError('No channel specified');
        setOutputData('');
        return;
      }

      const tokens = JSON.parse(localStorage.getItem('tokens')) || [];

      const index = tokens.findIndex((x) => x.id === selectedChannel);

      const request = await fetch(
        `https://api.twitch.tv/helix/moderation/moderators?broadcaster_id=${tokens[index].id}&user_id=${selectedUser.split('- ID: ')[1]}`,
        {
          method: 'DELETE',
          headers: {
            Authorization: `Bearer ${tokens[index].access_token}`,
            'Client-Id': tokens[index].client_id
          }
        }
      );

      if (!request.ok) {
        const data = await request.json();

        setOutputError(data.message);
        setOutputData('');
        return;
      }

      setOutputData('');
      setOutputData(`Successfully removed ${selectedUser.split('- ID: ')[0]} as Moderator in ${tokens[index].login}`);
      return;
    };

    const addVip = async () => {
      if (!selectedChannel) {
        setOutputError('No channel specified');
        setOutputData('');
        return;
      }

      const tokens = JSON.parse(localStorage.getItem('tokens')) || [];

      const index = tokens.findIndex((x) => x.id === selectedChannel);

      const request = await fetch(
        `https://api.twitch.tv/helix/channels/vips?broadcaster_id=${tokens[index].id}&user_id=${selectedUser.split('- ID: ')[1]}`,
        {
          method: 'POST',
          headers: {
            Authorization: `Bearer ${tokens[index].access_token}`,
            'Client-Id': tokens[index].client_id
          }
        }
      );

      if (!request.ok) {
        const data = await request.json();

        setOutputError(data.message);
        setOutputData('');
        return;
      }

      setOutputData('');
      setOutputData(`Successfully added ${selectedUser.split('- ID: ')[0]} as VIP in ${tokens[index].login}`);
      return;
    };

    const addMod = async () => {
      if (!selectedChannel) {
        setOutputError('No channel specified');
        setOutputData('');
        return;
      }

      const tokens = JSON.parse(localStorage.getItem('tokens')) || [];

      const index = tokens.findIndex((x) => x.id === selectedChannel);

      const request = await fetch(
        `https://api.twitch.tv/helix/moderation/moderators?broadcaster_id=${tokens[index].id}&user_id=${selectedUser.split('- ID: ')[1]}`,
        {
          method: 'POST',
          headers: {
            Authorization: `Bearer ${tokens[index].access_token}`,
            'Client-Id': tokens[index].client_id
          }
        }
      );

      if (!request.ok) {
        const data = await request.json();

        setOutputError(data.message);
        setOutputData('');
        return;
      }

      setOutputData('');
      setOutputData(`Successfully added ${selectedUser.split('- ID: ')[0]} as Moderator in ${tokens[index].login}`);
      return;
    };

    const fetchData = async () => {
      if (selectedType === 'add-moderator') {
        setAdditionalElement(
          <React.Fragment>
            <Grid item xs={12}>
              <Autocomplete
                options={searchResults}
                onChange={(event, value) => {
                  setSelectedUser(value);
                }}
                renderInput={(params) => (
                  <TextField
                    {...params}
                    fullWidth
                    id="standard-basic"
                    label="User"
                    variant="filled"
                    onChange={(event) => {
                      handleSearchChange(event.target.value);
                    }}
                  />
                )}
              />
            </Grid>
            <Grid item xs={12}>
              <Button variant="contained" fullWidth onClick={addMod}>
                Add
              </Button>
            </Grid>
          </React.Fragment>
        );
      } else if (selectedType === 'add-vip') {
        setAdditionalElement(
          <React.Fragment>
            <Grid item xs={12}>
              <Autocomplete
                options={searchResults}
                onChange={(event, value) => {
                  setSelectedUser(value);
                }}
                renderInput={(params) => (
                  <TextField
                    {...params}
                    fullWidth
                    id="standard-basic"
                    label="User"
                    variant="filled"
                    onChange={(event) => {
                      handleSearchChange(event.target.value);
                    }}
                  />
                )}
              />
            </Grid>
            <Grid item xs={12}>
              <Button variant="contained" fullWidth onClick={addVip}>
                Add
              </Button>
            </Grid>
          </React.Fragment>
        );
      } else if (selectedType === 'remove-moderator') {
        const mods = await getMods();

        const props = {
          options: mods
        };

        setAdditionalElement(
          <React.Fragment>
            <Grid item xs={12}>
              <Autocomplete
                {...props}
                id="disable-close-on-select"
                onChange={(event, value) => {
                  setSelectedUser(value);
                }}
                renderInput={(params) => <TextField {...params} label="Moderator" variant="filled" />}
              />
            </Grid>
            <Grid item xs={12}>
              <Button variant="contained" fullWidth onClick={removeMod}>
                Remove
              </Button>
            </Grid>
          </React.Fragment>
        );
      } else if (selectedType === 'remove-vip') {
        const vips = await getVips();

        const props = {
          options: vips
        };

        setAdditionalElement(
          <React.Fragment>
            <Grid item xs={12}>
              <Autocomplete
                {...props}
                id="disable-close-on-select"
                onChange={(event, value) => {
                  setSelectedUser(value);
                }}
                renderInput={(params) => <TextField {...params} label="VIP" variant="filled" />}
              />{' '}
            </Grid>
            <Grid item xs={12}>
              <Button variant="contained" fullWidth onClick={removeVip}>
                Remove
              </Button>
            </Grid>
          </React.Fragment>
        );
      }
    };

    fetchData();
  }, [selectedType, searchResults, selectedChannel, selectedUser]);

  const removeToken = async () => {
    if (!selectedChannel) {
      setOutputError('No channel specified');
      setOutputData('');
      return;
    } else {
      try {
        const tokens = localStorage.getItem('tokens') ? JSON.parse(localStorage.getItem('tokens')) : [];

        const index = tokens.findIndex((x) => x.id === selectedChannel);

        if (index === -1) {
          setOutputError('There are no channels');
          setOutputData('');
          return;
        }

        const data = tokens[index];

        tokens.splice(index, 1);
        localStorage.setItem('tokens', JSON.stringify(tokens));

        updateChannels();

        setOutputData('');
        setOutputData(`Successfully removed login: ${data.login} - ID: ${data.id}`);
        return;
      } catch (err) {
        setOutputError(err.message);
        setOutputData('');
        return;
      }
    }
  };

  const validateToken = async (token) => {
    const validateResponse = await fetch('https://id.twitch.tv/oauth2/validate', {
      headers: {
        Authorization: `OAuth ${token}`
      }
    });

    const validateData = await validateResponse.json();

    if (!validateResponse.ok) {
      switch (validateData.message) {
        case 'invalid access token':
          throw new Error('Invalid access token');
        default:
          throw new Error(validateData.message || 'Validate token failed');
      }
    }

    return validateData;
  };

  const openTokenGenerator = async () => {
    const type = 'token';
    const clientID = '87q7zikx7uny69x0wkxtf8bm4kd5m2';
    const redirectURI = window.location.origin ?? 'https://devlsco.github.io/';
    window.open(
      `https://id.twitch.tv/oauth2/authorize?response_type=${type}&client_id=${clientID}&redirect_uri=${redirectURI}&scope=channel:manage:vips+channel:manage:moderators&force_verify=true`,
      '_self'
    );
  };

  return (
    <MainCard title="Channel Manager" description="Add or remove moderators/VIPs from a specific channel.">
      <Grid container spacing={2} alignItems="center">
        <Grid item xs={12}>
          <TextField
            fullWidth
            id="standard-basic"
            label="OAuth Token"
            type="password"
            value={token}
            variant="filled"
            onChange={(e) => setToken(e.target.value)}
          />
        </Grid>
        <Grid item xs={6}>
          <Button variant="contained" style={{ background: '#2196f3' }} fullWidth onClick={addToken}>
            Add
          </Button>
        </Grid>
        <Grid item xs={6}>
          <Button variant="contained" style={{ background: '#ff0000' }} fullWidth onClick={removeToken}>
            Remove
          </Button>
        </Grid>
        <Grid item xs={12}>
          <Button
            variant="contained"
            style={{ background: 'hsla(0, 0%, 40%, .3)', border: '1px solid #2196f3' }}
            fullWidth
            onClick={openTokenGenerator}
          >
            Generate OAuth Token
          </Button>
        </Grid>
        <Grid item xs={6}>
          <FormControl fullWidth variant="filled">
            <InputLabel id="channel-label">Channel</InputLabel>
            <Select
              value={selectedChannel}
              onChange={(e) => setSelectedChannel(e.target.value)}
              labelId="channel-label"
              id="channel-select"
            >
              {menuItems.map((menuItem, index) => (
                <MenuItem key={menuItem.value} value={menuItem.value} selected={index === 1}>
                  {menuItem.label}
                </MenuItem>
              ))}
            </Select>
          </FormControl>
        </Grid>
        <Grid item xs={6}>
          <FormControl fullWidth variant="filled">
            <InputLabel id="type-label">Type</InputLabel>
            <Select value={selectedType} onChange={(e) => setSelectedType(e.target.value)} labelId="type-label" id="type-select">
              <MenuItem value="add-moderator">Add Moderator</MenuItem>
              <MenuItem value="add-vip">Add VIP</MenuItem>
              <MenuItem value="remove-moderator">Remove Moderator</MenuItem>
              <MenuItem value="remove-vip">Remove VIP</MenuItem>
            </Select>
          </FormControl>
        </Grid>
        {additionalElement}
        <Grid item xs={12}>
          {outputError && (
            <Typography variant="body2" style={{ color: 'red', marginTop: '10px', textAlign: 'center' }}>
              {outputError}
            </Typography>
          )}
          {outputData && (
            <Typography variant="body2" style={{ marginTop: '10px', textAlign: 'center' }}>
              {outputData}
            </Typography>
          )}
        </Grid>
      </Grid>
    </MainCard>
  );
};

export default Page;
